











































































import { Component, Vue } from "vue-property-decorator";
import {
  getAllProducts,
  publishedProduct,
  Product,
} from "../../services/ProductsService";
import { VueGoodTable } from "vue-good-table";

@Component({
  components: {
    VueGoodTable,
  },
})
export default class ProductsLists extends Vue {
  public isLoading = true;

  //  #region Columns
  public columns = [
    {
      label: this.$t("labels.name"),
      field: "name",
      sortable: true,
      thClass: "table-header-name",
      globalSearchDisabled: true,
      width: "20%",
    },
    {
      label: this.$t("labels.price"),
      field: "price",
      type: "number",
      sortable: true,
      formatFn: this.formatPrice,
    },
    {
      label: this.$t("labels.quantity"),
      field: "quantity",
      type: "number",
      sortable: true,
      firstSortType: "asc",
    },
    {
      label: "Categories",
      field: "categories",
      html: true,
      sortable: true,
      type: "number",
      thClass: "text-center",
      tdClass: "text-center",
    },
    {
      label: "Status",
      field: "isPublished",
      type: "boolean",
      sortable: true,
      firstSortType: "desc",
      // tdClass: this.isPublished,
      width: "20%",
      thClass: "text-center",
      tdClass: "text-center",
    },
    {
      label: "Action",
      field: "action",
      html: true,
      sortable: false,
      width: "20%",
      thClass: "text-center",
      tdClass: "text-center",
    },
  ];

  //  #endregion

  public rows: Array<Partial<Product>> = [];

  public totalRecords: number = 0;

  public serverParams = {
    columnFilters: {},
    sort: [
      {
        field: "",
        type: "",
      },
    ],
    page: 1,
    perPage: 10,
  };

  public mounted(): void {
    this.loadItems();
    this.$parent.$on("refresh", this.refresh);
  }

  public refresh(): void {
    this.loadItems();
  }

  public onPageChange(params: any): void {
    this.updateParams({ page: params.currentPage });
    // this.loadItems();
  }

  public updateParams(newProps: any): void {
    this.serverParams = Object.assign({}, this.serverParams, newProps);
  }

  public onPerPageChange(params: any): void {
    //  this.updateParams({perPage: params.currentPerPage});
    //  this.loadItems();
  }

  public onSortChange(params: any): void {
    /*    this.updateParams({
        sort: [{
          type: params.sortType,
          field: this.columns[params.columnIndex].field,
        }],
      });
      this.loadItems(); */
  }

  public formatPrice(value: string): string {
    return value + " $";
  }

  public onColumnFilter(params: any): void {
    this.updateParams(params);
    this.loadItems();
  }

  public loadItems(): void {
    getAllProducts()
      .then((result: Array<Product>) => {
        this.$nextTick(function () {
          this.rows = result;
          this.totalRecords = result.length;
          this.isLoading = false;
        });
      })
      .catch((error: any) => console.error("Errors : ", error));
  }

  public publish(productId: string, publish: boolean): void {
    const idx = this.rows.findIndex((prod) => prod.productId === productId);
    publishedProduct(productId, publish)
      .then((result: any) => {
        this.rows[idx].isPublished = publish;
      })
      .catch((error: any) => console.error("Error:", error));
  }
}
