


























import { Component, Prop, Vue } from "vue-property-decorator";

@Component({
  components: {},
})
export default class ProductCard extends Vue {
  public isLoading = false;
  public source: string = "default";

  get image(): string {
    var images = require.context(
      "../../../public/images/",
      false,
      /\.(png|jpg|jpeg|gif)$/i
    );
    let img = "diamond";
    return images("./" + img + ".png");
  }
}
